import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

import Header from '../components/Header/Header';
import Button from '../components/Button/Button';

import lockerIcon from '../assets/img/Locker.svg';
import envelopeIcon from '../assets/img/Envelope.svg';
import loaderIcon from '../assets/img/loading.gif';

export default function PassReset() {
  const [email, setEmail] = useState('');

  const [loading, setLoading] = useState(false);
  const [isReset, setIsReset] = useState(false);

  const [error, setError] = useState('');
  const [valid, setValid] = useState(true);

  function checkEmail() {
    if (email.trim() !== '' && email === 'example@example.com') {
      setLoading(true);
      setError('');
      setValid(true);
      setTimeout(() => {
        setLoading(false);
        setIsReset(true);
      }, 2000);
    }

    if (email.trim() === '') {
      setError('–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É');
      setValid(false);
    }

    if (email.trim() !== '') {
      if (email !== 'example@example.com') {
        setValid(false);
        setError('–ü–æ—á—Ç–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞');
      }
    }
  }

  console.log(loading);

  return (
    <div>
      <Header />

      {!isReset ? (
        <div className='content locker-content'>
          <img src={lockerIcon} alt='Locker Icon' />
          <h2>–í–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø–∞—Ä–æ–ª—å</h2>
          <span>–í–≤–µ–¥–∏—Ç–µ e-mail, –Ω–∞ –∫–æ—Ç–æ—Ä—ã–π —Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å —Ä–∞–Ω–µ–µ</span>

          <div className='content-inputs'>
            <input
              onChange={(e) => setEmail(e.target.value)}
              className={!valid ? 'wrong' : ''}
              value={email}
              type='email'
              placeholder='–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É'
              disabled={loading}
              required
            />
            {error && <p className='error'>{error}</p>}
          </div>

          <Button icon={loading && loaderIcon} class='blue-button' onClick={checkEmail}>
            {!loading ? '–û—Ç–ø—Ä–∞–≤–∏—Ç—å' : '–û—Ç–ø—Ä–∞–≤–∫–∞'}
          </Button>
          <NavLink to='/'>–û—Ç–º–µ–Ω–∏—Ç—å</NavLink>
        </div>
      ) : (
        <div className='content locker-content'>
          <img src={envelopeIcon} alt='Envelope Icon' />
          <h2>–ü–∏—Å—å–º–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ</h2>
          <span>–ù–∞ —É–∫–∞–∑–∞–Ω–Ω—ã–π –≤–∞–º–∏ e-mail –±—ã–ª–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –ø–∏—Å—å–º–æ –¥–ª—è —Å–º–µ–Ω—ã –ø–∞—Ä–æ–ª—è</span>

          <NavLink to='/'>
            <Button class='blue-button'>–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞ –≥–ª–∞–≤–Ω—É—é</Button>
          </NavLink>
        </div>
      )}
    </div>
  );
}
