/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import Button from '../components/Button/Button';
import Header from '../components/Header/Header';

import facebook from '../assets/img/FacebookLogo.svg';
import google from '../assets/img/GoogleLogo.svg';

export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [coupon, setCoupon] = useState(false);

  const [valid, setValid] = useState(true);
  const [error, setError] = useState('');
  const [isEmailValid, setIsEmailValid] = useState(true);

  const [isReg, setIsReg] = useState(false);

  const regExpEmail = /[A-Z0-9._%+-]+@[A-Z0-9-]+.+.[A-Z]{2,4}/gim;

  function checkDataToLogin() {
    if (email.trim() !== '' && password !== '' && name.trim() !== '') {
      if (regExpEmail.test(email.toLowerCase())) {
        setIsEmailValid(true);
        setError('');
        setValid(true);
        setIsReg(true);
      } else {
        setIsEmailValid(false);
        setValid(true);
        setError('–í–æ–∑–º–æ–∂–Ω–æ –≤—ã –æ—à–∏–±–ª–∏—Å—å –≤ —É–∫–∞–∑–∞–Ω–∏–∏ –ø–æ—á—Ç–æ–≤–æ–≥–æ —Å–µ—Ä–≤–∏—Å–∞');
      }
    } else {
      setError('–í–≤–µ–¥–∏—Ç–µ –≤—Å–µ –¥–∞–Ω–Ω—ã–µ');
      setValid(false);
    }
  }

  return (
    <div>
      <Header type='reg' />

      <div className='auth-content content'>
        <h1>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h1>
        <span>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–π—Å—è –∏ –ø–æ–ª—É—á–∏ –¥–æ—Å—Ç—É–ø –∫ –∞–Ω–∞–ª–∏—Ç–∏–∫–µ –∞–∫–∫–∞—É–Ω—Ç–æ–≤. </span>
        <div className='auth-content-btns'>
          <Button icon={facebook} class='social-button'>
            –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ Facebook
          </Button>

          <Button icon={google} class='social-button'>
            –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ Google
          </Button>
        </div>
        <span>–∏–ª–∏</span>
        <div className='content-inputs'>
          <input
            onChange={(e) => setName(e.target.value)}
            className={!valid ? 'wrong' : ''}
            type='text'
            value={name}
            placeholder='–ò–º—è'
            required
          />
          <input
            className={!isEmailValid || !valid ? 'wrong' : ''}
            onChange={(e) => setEmail(e.target.value)}
            type='email'
            value={email}
            placeholder='Email'
            required
          />
          {!isEmailValid && <p className='error'>{error}</p>}
          <input
            className={!valid ? 'wrong' : ''}
            onChange={(e) => setPassword(e.target.value)}
            type='password'
            value={password}
            placeholder='–ü–∞—Ä–æ–ª—å'
            required
          />

          {!coupon ? (
            <a onClick={() => setCoupon(true)}>–£ –º–µ–Ω—è –µ—Å—Ç—å –ø—Ä–æ–º–æ–∫–æ–¥</a>
          ) : (
            <input type='text' placeholder='–ü—Ä–æ–º–æ–∫–æ–¥' />
          )}

          {error && isEmailValid && <p className='error'>{error}</p>}
        </div>
        <Button class='blue-button' onClick={checkDataToLogin}>
          –°–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç
        </Button>
        –°–æ–∑–¥–∞–≤–∞—è –∞–∫–∫–∞—É–Ω—Ç, —è —Å–æ–≥–ª–∞—Å–µ–Ω —Å <a>—É—Å–ª–æ–≤–∏—è–º–∏ –æ—Ñ–µ—Ä—Ç—ã</a>
      </div>

      {isReg && (
        <Redirect
          to={{
            pathname: '/confirmation',
            state: { name: name, email: email },
          }}
        />
      )}
    </div>
  );
}
